					</div>
					<div class="form_list_right">
						<el-select ref="selectProvince" :disabled="isDisabledEditable" v-model="fundInfo.create_user_id" filterable clearable placeholder="请选择" @change='chooseCreater'>
						    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						是否隐藏
					</div>
					<div class="form_list_right flex_start">
						<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[fundInfo.is_hide?'radio_btn_active':'']">
							是
						</div>
						<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!fundInfo.is_hide?'radio_btn_active':'']">
							否
						</div>
					</div>
				</div>
				<div class="form_list flex_start" v-if="fundInfo.is_master == 0" >
					<div class="form_list_left">
					</div>
					<div class="form_list_right flex_start">
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						历史倍数
					</div>
					<div class="">
						<div class="histor_multiple_box table " style="max-width:745px">
							<el-table  :data="tableData" border  >
								<el-table-column v-for="(tableHeaderitem,index) of tableHeader " :key="index" :prop="index.toString()" :label="tableHeaderitem" width='120'>
									<template slot-scope="scope">
										<el-input  v-model="scope.row[index]" :disabled="isDisabledEditable" clearable @blur="historyCheck(scope.row[index],index)">
										</el-input>
									</template>
								</el-table-column>
								<el-table-column fixed="left" label="年份" width="70">
									<template slot-scope="scope">
										<span>倍数</span>
									</template>
								</el-table-column>
							</el-table>
						</div>
					</div>
				</div>
			</div>
			<!--表单按钮模块-->
			<div v-if="!isDisabledEditable" class="form_submit flex_end">
				<button @click="cancel">取消</button>
				<button @click="saveSelfFundEdit">保存</button>
			</div>
		</div>
		<!--公司工商信息模块开始-->
		<div class="company_background">
			<div class="block_title flex_between">
				<div class="title_left flex_start">
					<div class="radio"> </div>
					<div class="block_name">
						合伙企业信息
					</div>
				</div>
				<div class="title_right flex_end">
					<div class="hover_btn cursor flex_center" @click="downloadEquityInfo()">
						<span class="iconfont icon-daoru cursor" ></span>
						<span>导出股权穿透表</span>
					</div>
					<div class="blue_btn cursor flex_center"  @click="downLoadCompanyInfo()" >
						<span class="iconfont icon-daoru cursor"></span>
						<span>导出企业信息</span>
					</div>
				</div>
			</div>
			<company-detail-background v-bind:type="type"></company-detail-background>
		</div>
		<!-- 弹出框模块开始 -->
		<el-dialog custom-class="pop" append-to-body title="查看" :visible.sync="dialogVisible" width="630px" center>
			<div class="dialog_body table">
				<el-table :data="alertData" border style="width: 100%" empty-text="">
					<el-table-column prop="index" label="序号" width='80' sortable>
					</el-table-column>
					<el-table-column prop="name" label="投资人名称"  sortable>
					</el-table-column>
					<el-table-column prop="subscribe" label="实缴金额（万元）"  sortable>
					</el-table-column>
					<el-table-column prop="percent" label="认缴比例（%）"  sortable>
					</el-table-column>
				</el-table>
			</div>
		</el-dialog>
