</template>

<script>
	import companyDetailBackground from '../../pages/companyBackground/companyDetailBackground.vue'
	import url from '../../config/url'
	
	import { dialogConfirm, dialogConfirmHasbtn,isValidNumber ,isValidDecimal} from "../../config/mUtils";
	import { getSelfFundDetailApi,getSelfAddInitApi,getUserlistApi,saveSelfFundEditApi,downloadEquityInfoApi } from '../../config/api';
	import { ShareholderGet,getMasterPetfund } from '../../config/apiSj'
	import { format_money } from '../../config/mUtils'

	
	export default {
		name: 'selfAdministerFundCompanyDetail',
		inject: ['reload'],
		data() {
			return {
				id: '',
				allUserList: [],
				all_investor: [],
				all_sg_company_manager_list: [],//管理公司
				all_record_state: [],//备案状态
				all_fund_state: [],//基金状态
				all_collect_state: [],//募集状态
				duration: ['','',''],
				create_user_name: '',//数据创建人姓名
				fundName: '',//标题的基金名称
				fundInfo: {},
				type: 5,//舆情固定参数
				fundType: [
					{
						value: 1,
						label: '主体自管基金'
					},
					{
						value: 0,
						label: '分支自管基金'
					}
				],//基金类型
				business_status: [
					{
						value: 0,
						label: '正常'
					},
					{
						value: 1,
						label: '待变更'
					}
				],//工商状态
				coinList: [
					{
						value: 1,
						label: '人民币'
					},
					{
						value: 2,
						label: '美元'
					}
				],
				
				
				isDisabledEditable: true,
				isHide: 1,//表单字段是否隐藏
				// 表单字段测试数据

				alertData:[],//弹窗数据//
				dialogVisible: false,
				masterMotherFundList:[],

				//表头数据
				tableHeader:[],
				//表格数据
				tableData: [{}],
			}
		},
		created() {
			this.id = parseInt(this.$route.query.id);
			this.getSelfFundDetail();
			this.getSelectData();
			this.getAllUserList();
			this.toGetParentFof();
		},
		mounted() {},
		components: {
			companyDetailBackground
		},
		methods: {
			historyCheck(multiple,index){
				if(Number(multiple)!=multiple || multiple<0){
					dialogConfirm('info','历史倍数格式错误或小于0');
					this.tableData[0][index] = 0;
				}
				if(this.tableData[0][index]==''){
					this.tableData[0][index] = 0;
				}
			},
			getSelfFundDetail(){
				getSelfFundDetailApi({
					id: this.id
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.fundInfo = res.data.payload.data;
						this.duration = res.data.payload.data.duration.split('+');
						if(this.duration.length==1){
							this.duration[1] = '';
							this.duration[2] = '';
						}else if(this.duration.length==2){
							this.duration[2] = '';
						}
						this.create_user_name = res.data.payload.data.create_user_name;
						this.fundName = res.data.payload.data.fundName;
						this.fundInfo.subscription_amount = format_money(this.fundInfo.subscription_amount)
						this.fundInfo.amount_paid_in = format_money(this.fundInfo.amount_paid_in);
						if(!this.fundInfo.investor_id){
							this.fundInfo.investor_id = '';
						}
						if(!this.fundInfo.sg_company_manager_id){
							this.fundInfo.sg_company_manager_id = '';
						}
						if(!this.fundInfo.create_user_id){
							this.fundInfo.create_user_id = '';
						}
						

						if(this.fundInfo.is_master===1){
							this.fundInfo.p_petfund_id = '';
						}else{
							if(this.fundInfo.p_petfund_id===0){
								this.fundInfo.p_petfund_id = '';
							}
						}

						this.tableHeader = [];
						this.tableData = [{}],
						this.fundInfo.subscription_history.forEach((item,index) => {
							this.tableHeader.push(item.year.toString());
							this.tableData[0][index] = item.multiple;
						});

					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			saveSelfFundEdit(){
				// 判断odi额度是否大于0
				// if (parseFloat(this.fundInfo.odi) < 0 || (!parseFloat(this.fundInfo.odi))) {
				// 	dialogConfirm('info', 'ODI额度应不小于0');
				// 	return
				// }
				if(this.fundInfo.odi!=''){
					if(!isValidDecimal(this.fundInfo.odi)||parseFloat(this.fundInfo.odi) < 0){
						dialogConfirm('info', 'ODI额度应大于等于0');
						this.fundInfo.odi = '';
						return
					}
				}
				//验证合伙企业、基金简称、管理公司比填
				if(this.fundInfo.fundName&&this.fundInfo.simpleName&&this.fundInfo.sg_company_manager_id){
					//处理 基金期限（年）的数据格式

					var is_valid_number = this.isValidFundTime();
					if (is_valid_number) {

						var duration = JSON.parse(JSON.stringify(this.duration));
						if(duration[1]==''&&duration[2]==''){
							duration = duration[0];
						}else if(duration[1]==''&&duration[2]!=''){
							dialogConfirm("info", '基金期限不能为空!');
							return;
						}else if(duration[1]!=''&&duration[2]==''){
							duration.splice(2,1);
							duration = duration.join('+');
						}else{
							duration = duration.join('+');
						}


						// var duration = this.duration.join('+');
					} else {
						return
					}
					// console.log(duration);
					// return false;
					var info = {
						id: this.id,
						fundName: this.fundInfo.fundName,
						simpleName: this.fundInfo.simpleName,
						is_master: this.fundInfo.is_master,
						investor_id: this.fundInfo.investor_id,
						sg_company_manager_id: this.fundInfo.sg_company_manager_id,
						unit: this.fundInfo.unit,
						fund_date: this.fundInfo.fund_date,
						odi: this.fundInfo.odi,
						duration: duration,
						protocol_end_date: this.fundInfo.protocol_end_date,
						record_state: this.fundInfo.record_state,
						record_state_number: this.fundInfo.record_state_number,
						create_user_id: this.fundInfo.create_user_id,
						fund_state: this.fundInfo.fund_state,
						collect_state: this.fundInfo.collect_state,
						is_hide: this.fundInfo.is_hide,
						create_user_name: this.create_user_name,
						check_multiple:this.fundInfo.check_multiple
					}

					if(this.fundInfo.is_master===0){
						if(this.fundInfo.p_petfund_id){
							info.p_petfund_id = this.fundInfo.p_petfund_id;
						}else{
							info.p_petfund_id = 0;
						}
					}else{
						info.p_petfund_id = 0
					}

					let historyArr = [];
					this.tableHeader.forEach((element,index) => {
						let newObj = {
							year:element,
							multiple:this.tableData[0][index]?this.tableData[0][index]:'0',
						};
						historyArr.push(newObj);
					});
					if(historyArr.length){
						info.history = JSON.stringify(historyArr);
					}else{
						info.history = '';
					}
					saveSelfFundEditApi(info).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.reload();
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}else{
					if (!this.fundInfo.fundName) {
						dialogConfirm("info", '合伙企业不能为空');
					}else if(!this.fundInfo.simpleName){
						dialogConfirm("info", '基金简称不能为空');
					}else if(!this.fundInfo.sg_company_manager_id){
						dialogConfirm("info", '管理公司不能为空');
					}
				}
			},
			// 判断基金期限是否有效
			isValidFundTime(){
				if (this.duration[0]) {
					let is_valid_fund_expire1 = isValidNumber(this.duration[0]);
					let is_valid_fund_expire2 = isValidNumber(this.duration[1]);
					let is_valid_fund_expire3 = isValidNumber(this.duration[2]);
					if(this.duration[1]&&!is_valid_fund_expire2){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if(this.duration[2]&&!is_valid_fund_expire3){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if (is_valid_fund_expire1) {
						return true
					} else{
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
				} else{
					dialogConfirm("info", '基金期限不能为空');
					return false
				}
			},
			// 页面刷新
			cancel() {
				this.reload();
			},
			// 获取创建人姓名
			chooseCreater(e){
				this.$nextTick(function () {
					this.create_user_name = this.$refs.selectProvince.selected.label;
		        });
			},
			// 获取投资人列表
			getSelectData(){
				getSelfAddInitApi({
					
				}).then(res => {
					this.all_investor = res.data.payload.all_investor;
					this.all_record_state = res.data.payload.all_record_state;//备案状态
					this.all_fund_state = res.data.payload.all_fund_state;
					this.all_collect_state = res.data.payload.all_collect_state;
					this.all_sg_company_manager_list = res.data.payload.all_sg_company_manager_list;//管理公司
				})
			},
			// 获取所有人员列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			changeEditStatus() {
				this.isDisabledEditable = false;
				this.$nextTick(function () {
		        		this.$refs.input1.$el.querySelector('input').focus();
		        });
			},
			changeHideStatus(e){
				if (!this.isDisabledEditable) {
					if (e == 1) {
						this.fundInfo.is_hide = 1;
					} else{
						this.fundInfo.is_hide = 0;
					}
				}
			},

			// 获取工商状态带变更数据
			getSatateList(){
				ShareholderGet({
					company_name:this.fundInfo.fundName,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload;
						data.forEach((element,index) => {
							element.index = index+1;
						});
						this.dialogVisible = true;
						this.alertData = data;
					}else{
						dialogConfirm('info', res.data.message);
					}
				})
			},

			//下载股权穿透图
			downloadEquityInfo(){
				downloadEquityInfoApi({
					company_name: this.fundInfo.fundName
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			
			//导出企业信息
			downLoadCompanyInfo(){
				let key = window.localStorage.getItem("htcx_server_key");
				let href = url.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.id + '&key=' + key + '&new_type=' + this.type;
				window.location.href = href;
			},
			//获取所有的主体母自管基金
			toGetParentFof(){
				getMasterPetfund().then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.data;
						//console.log(res);
						this.masterMotherFundList = data;
					}
				})
			},

			//计算出来对应的历史倍数年限
			toCalcYear(){
				this.tableHeader = [];
				this.tableData = [{}];
				if(this.fundInfo.fund_date&&this.fundInfo.protocol_end_date){
					let  fund_date = Date.parse(this.fundInfo.fund_date);//工商设置日期
					let  end_date = Date.parse(this.fundInfo.protocol_end_date);//基金到期日
					if(fund_date <= end_date){
						let fund_year = this.fundInfo.fund_date.split('-')[0];
						let end_year = this.fundInfo.protocol_end_date.split('-')[0];
						let number = parseInt(end_year) - parseInt(fund_year) ;
						for(let i = 0;i<=number;i++){
							this.tableHeader.push((parseInt(fund_year)+i).toString());
						}
					}else{
						this.fundInfo.fund_date = '';
						dialogConfirm('info','工商设立日期不能大于基金到期日');
					}
				}
				
				if(this.tableHeader.length){
					let tableDataObj = [{}];
					this.tableHeader.forEach((element,index) => {
						tableDataObj[0][index] = 0;
					});
					this.tableData=tableDataObj;
				}
				
			},
			//去基金运行
			toFundRunPage(){
				this.$router.push({
					name: "selfAdministerFundRun",
					query: {
						id: this.id,
						type: 2
					}
				})
			},
		},
	}
</script>

<style scoped="scoped">
	.company {
		box-sizing: border-box;
		padding: 0 30px;
		font-size: 14px;
	}
	.company_header {
		color: #33302D;
		font-size: 18px;
		padding: 20px 0px;
	}
	
	.company_name {
		border-bottom: 1px solid #EAEAEA;
		padding: 0px 0px 20px 0
	}
	.company_name_left {
		font-size: 16px;
		color: #33302D;
	}
	.company_name_right {
		margin-left: 80px;
		color: #6292F4;
	}
	.company_name_right i,.company_name_right span {
		color: #6292F4;
	}
	.company_name_right i{
		margin-left: 8px;
	}
	/*公司信息表单模块*/
	.company_info {
		padding: 8px 5px 0 20px;
	}
	.form{
		flex-wrap: wrap;
		padding: 30px 0;
		color: #6F7071;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 19px;
	}
	.form_list_left{
		width: 156px;
		color: #6F7071;
	}
	.form_list_right{
		width: 270px;
    	height: 40px;
	}
	.form_list_right1{
		width: 165px;
		margin-right: 10px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn1{
		width: 84px;
		height: 40px;
		margin-right: 0px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
		color: #6292F4;
	}
	.radio_btn2{
		width: 84px;
		height: 40px;
		margin-right: 0px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #ffffff;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-left: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}
	/*工商信息等公司背景*/
	.company_background {
		margin-top: 60px;
	}
	.company_info_edit_btn{
		margin-top: 70px;
	}
	.company_info_edit_btn button{
		width: 94px;
		height: 36px;
		color: white;
		border-radius: 20px;
		background: #6292F4;
	}
	.company_info_edit_btn button:nth-child(1){
		margin-right: 28px;
	}
	
	.block_title {
		margin-bottom: 25px;
		padding-bottom: 10px;
		border-bottom: 1px solid #CECBC9;
	}
	
	.title_left>div {
		color: #6292F4;
		font-size: 16px;
	}
	
	.title_left .radio {
		width: 7px;
		height: 7px;
		margin-right: 12px;
		border-radius: 50%;
		background: #6292F4;
	}
	
	.title_right>div {
		color: #6292F4;
	}
	
	.title_right i {
		color: #6292F4;
	}
	.title_right .blue_btn{
		margin-left: 20px;
	}
</style>


